import { makeElement } from './template_generation_tools'
import { tabbedControls } from './tabbed_controls'

export const mediaContainer = function mediaContainer (data = {}) {
  const wrapper = makeElement('div', ['kubik_media_wrapper', 'kubik-wysiwyg-component'])
  const imageContents = makeElement('div', ['kubik_media_image_wrapper'])
  const imageContentsLabel = makeElement('label', ['kubik_media_image_label'])
  imageContentsLabel.innerText = 'Add image hello'
  imageContents.appendChild(imageContentsLabel)
  const tabs = tabbedControls('media_type', [ { key: 'image', label: 'Image', contents: imageContents}, { key: 'video', label: 'type'} ])
  debugger
  wrapper.appendChild(tabs)
  return wrapper
}
